<template>
  <v-app>
    <div>
      <v-main>
        <app-bar />
        <router-view />
      </v-main>
    </div>
    <staticFooter />

    <!-- <v-snackbar top v-model="snackbar.showing" :timeout="-1">
      {{ snackbar.text }}

      <template v-slot:action="{ attrs }">
        <v-btn
          color="blue"
          text
          v-bind="attrs"
          @click="snackbar.showing = false"
        >
          Close
        </v-btn>
      </template>
    </v-snackbar> -->

    <v-snackbar v-model="snackbar.showing" top :timeout="5000" color="indigo">
      {{ snackbar.text }}

      <v-btn text @click="snackbar.showing = false"> Close </v-btn>
    </v-snackbar>
  </v-app>
</template>

<script>
import { mapState } from "vuex";
import staticFooter from "./components/staticFooter.vue";
import appBar from "./components/appBar.vue";

export default {
  components: {
    staticFooter,
    appBar,
  },

  computed: {
    currentUser() {
      return this.$store.state.auth.user;
    },
    ...mapState(["snackbar"]),
  },
    mounted() {
    if (!this.currentUser) {
      this.$router.push("/login");
    }
  },
};
</script>
<style>
.v-card--material {
  position: relative;
  /* top: -64px; */
  top: 20px;
  /* margin-bottom: -32px; */
}

.v-cardheading {
  position: relative;
  top: -25px;
  transition: 0.3s ease;
  z-index: 1;
  text-align: end;
}
</style>
