import Vue from 'vue';
import Vuex from 'vuex';

import { auth } from './auth.module';

Vue.use(Vuex);

export default new Vuex.Store({

  state: {
    snackbar: {}
  },

  mutations: {
    SET_SNACKBAR(state, snackbar) {
      state.snackbar = snackbar;
    },
  },
  actions: {
    setSnackbar({commit}, snackbar) {
      commit('SET_SNACKBAR', snackbar);
    },
  },


  modules: {
    auth,
    // snackbar,
  },
});
